"use client";

import { useState } from "react";

export default function UploadPage() {
  const [file, setFile] = useState(null);
  const [uploading, setUploading] = useState(false);
  const [message, setMessage] = useState("");

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
    setMessage("");
  };

  const handleUpload = async () => {
    if (!file) {
      setMessage("⚠️ Please choose a file first!");
      return;
    }

    setUploading(true);
    setMessage("");

    const formData = new FormData();
    formData.append("file", file);

    try {
      const res = await fetch("http://localhost:8000/upload", {
        method: "POST",
        body: formData,
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.detail || "Upload failed");
      if (data.status === "success") {
        setMessage(`✅ Uploaded: ${data.filename || file.name}. ${data.message || ""}`);
      } else {
        setMessage("❌ Upload failed. " + (data.detail || ""));
      }      

    } catch (err) {
      console.error(err);
      setMessage("❌ Upload failed. Check server logs.");
    } finally {
      setUploading(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-black text-white p-6">
  <div className="w-full max-w-md bg-gray-900 p-6 rounded-2xl">
    <h2 className="text-2xl font-semibold text-purple-300 mb-4">Upload Document</h2>

    {/* Custom file upload button */}
    <label className="w-full flex items-center justify-center px-4 py-2 mb-3 bg-gray-800 text-gray-300 rounded cursor-pointer hover:bg-gray-700 border border-gray-600">
      Choose File
      <input
        type="file"
        onChange={handleFileChange}
        className="hidden"   // hide the default ugly input
      />
    </label>

    <button
      onClick={handleUpload}
      disabled={uploading}
      className="w-full bg-purple-600 hover:bg-purple-700 py-2 rounded text-white mb-2 disabled:opacity-60"
    >
      {uploading ? "Uploading..." : "Upload"}
    </button>

    {message && <div className="p-3 rounded bg-gray-800 mt-2">{message}</div>}

    <a
      href="/ask"
      className="block text-center mt-4 bg-green-600 hover:bg-green-700 py-2 rounded text-white"
    >
      Go to Ask Page
    </a>
  </div>
</div>

  );
}
